<template>
  <div class="start-list">
    <div class="wrap-loader" v-if="isLoading">
      <div id="loader"></div>
    </div>
    <h3 class="xlsx-download__title">    XLSX</h3>
    <input
      type="file"
      ref="fileupload"
      @change="onChange"
      id="file"
      class="inputfile"
    />
    <label for="file"> </label>
    <span v-if="this.file === null">  </span>
    <div class="select-block__item-block">
      <label for="standard-select"> </label>
      <div class="select">
        <select required id="standard-select" v-model="nominate">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <span class="focus"></span>
      </div>
    </div>
    <div class="select-block__item-block">
      <label for="standard-select">  </label>
      <div class="select">
        <select required id="standard-select" v-model="age">
          <option value="."></option>
          <option value="."></option>
          <option value="6-8">6-8</option>
          <option value="2014">2014 </option>
          <option value="2015">2015 </option>
          <option value="2016">2016 </option>
          <option value="9-11">9-11</option>
          <option value="2011">2011 </option>
          <option value="2012">2012 </option>
          <option value="2013">2013 </option>
          <option value="12-14">12-14</option>
          <option value="2008">2008 </option>
          <option value="2009">2009 </option>
          <option value="2010">2010 </option>
          <option value="15-17">15-17</option>
          <option value="2005">2005 </option>
          <option value="2006">2006 </option>
          <option value="2007">2007 </option>
          <option value="18+">18+</option>
        </select>
        <span class="focus"></span>
      </div>
    </div>
    <div class="xlsx-download__input">
      <input
        type="submit"
        value=" "
        class="button btn-primary"
        @click="onSubmitProtocol()"
        :disabled="!this.file"
      />
    </div>
    <xlsx-read :file="file" v-if="this.file !== null" class="xlsx-result">
      <xlsx-table />
    </xlsx-read>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { actionTypes } from "@/store/modules/board";
import { XlsxRead, XlsxTable } from "vue-xlsx";
export default {
  name: "AppPostBoardData",
  data() {
    return {
      file: null,
      nominate: "",
      age: "",
    };
  },
  computed: {
    ...mapState({
      isLoading: (state) => state.board.isLoading,
    }),
  },
  components: {
    XlsxRead,
    XlsxTable,
  },
  methods: {
    onChange(event) {
      this.file = event.target.files ? event.target.files[0] : null;
    },
    onSubmitProtocol() {
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("nominate", this.nominate);
      formData.append("age", this.age);
      this.$store
        .dispatch(actionTypes.postScorePlayers, formData)
        .then(() => {
          (this.file = null), (this.$refs.fileupload.value = null);
          this.nominate = null;
          this.age = null;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style></style>
